<template>
  <div>
    <!--顶部配置信息-->
    <h_topConfig @webApiConfigFunc="webApiConfigFunc" @selectMachineFunc="selectMachineFunc" @reconnectWSFunc="reconnectWSFunc" :machines="machines" @loadMachineDataFunc="loadMachineDataFunc"></h_topConfig>
    <div id="body_content">
      <div class="body_content_center">
        <div class="left_btns">
          <h_sidebarBtnGroup :webApiConfig="webApiConfig" @selectWebApiFunc="selectWebApiFunc" :receiveMsg="receiveMsg" @clickWebApiBtnFunc="clickWebApiBtnFunc"></h_sidebarBtnGroup>
        </div>
        <div class="right_con">
          <h_paramsMgr :curSelectWebApi="curSelectWebApi" :curMachine="curMachine" :receiveMsg="receiveMsg" :isClickWebApiBtn="isClickWebApiBtn" @clickWebApiBtnHandleFunc="clickWebApiBtnHandleFunc"></h_paramsMgr>
        </div>
      </div>
    </div>

    <h_footerBar :toFooterMsg="toFooterMsg"></h_footerBar>
  </div>
</template>


<style>
  #body_content .body_content_center
  {
    width: 1000px;
    height: 100%;
    margin: 0 auto;
    margin-bottom: 30px;
  }
  #body_content .body_content_center .left_btns{
    float: left;
    width: 200px;
    height: 100%;
  }
  #body_content .body_content_center .right_con{
    float: left;
    width: 800px;
    height: 100%;
  }
  #body_content .body_content_center::after{
    content: "";
    clear: both;
    display: block;
  }
</style>
<script>
import sidebarBtnGroup from './components/sidebarBtnGroup'
import paramsMgr from './components/paramsMgr'
import topConfig from './components/topConfig'
import footerBar from './components/footerBar'


export default {
  components: {
    'h_sidebarBtnGroup': sidebarBtnGroup,
    'h_paramsMgr': paramsMgr,
    'h_topConfig': topConfig,
    'h_footerBar': footerBar
  },
  beforeCreate(){
    var that = this;
    that.webSocketIsOpen = false;
    this.WEB_SOCKET = null;
  },
  created(){
    this.toReceiveMachine();
  },
  data(){
    return {
      webApiConfig: null,               //webApi Config
      curSelectWebApi: null,            //当前选择的WebApi
      isClickWebApiBtn: false,          //是否点击了侧边栏的按钮Flag
      machines: [],                     //所有的专机
      recodeData: null,                 //记录的数据，用于保存 == TO DO。。。
      curMachine: null,                 //当前的专机
      receiveMsg: [],                   //接受到的消息 来自于WebSocket
      toFooterMsg: "",                  //底部信息栏的数据
      webSocketIsOpen: false,           //WebSocket是否连接
      explainMsg: ""                    //用于通知窗体的信息
    }
  },
  methods:{
    webApiConfigFunc(data){ this.webApiConfig = data;},   
    selectWebApiFunc(data){ this.curSelectWebApi = data;},
    selectMachineFunc(data){  this.curMachine = data;},
    clickWebApiBtnFunc(data){ this.isClickWebApiBtn = data;},
    clickWebApiBtnHandleFunc(data){ this.isClickWebApiBtn = data;},
    loadMachineDataFunc(data){ this.machines = data;},
    /********************    请求Machines  ********************************************/ 
     async toReceiveMachine(){
        var params = this.nethelp.toPackJsonStr("Web", "Web.Machine.LoadMachineInfo", { });
        await this.axios.post(this.nethelp.toServeUrl, params, {
            headers: {
            "Content-Type": "application/json",
            'Access-Control-Allow-Origin':'*'
            }
        }).then((response) => {
            console.log(response.data);
            if(response.data.success == false){
              this.$message({
                showClose: true,
                message: response.data.msg,
                type: 'error',
                duration: 0
              });
            }
            else
            {
              let machines = response.data.result.machines;
              for(var i = 0; i < machines.length; i++){
                let machine = {
                  machineId: machines[i]["id"],
                  machineName: machines[i]["name"],
                  machineVersion: machines[i]["version"]
                };
                this.machines.push(machine);
              }
            }
        }).catch((error) => {
            alert(this.nethelp.toServeUrl + "  " +  "Web.Machine.ReceiveRes" +  error);
        });
    },
    //连接WebSocket
    reconnectWSFunc(){this.connectWS();},
    connectWS(){
      var that = this;
      if(that.webSocketIsOpen == true){
        this.$notify({
          title: '警告',
          message: that.explainMsg,
          type: 'warning'
        });
        return;
      }
      that.webSocketIsOpen = true;
      this.WEB_SOCKET = new WebSocket(this.webSocketUrl + '/ws');
      that.toFooterMsg = "[WebSocket] 连接中...";
      that.explainMsg = "WebSocket正在连接中...";
      this.WEB_SOCKET.onopen = function (evt) {
          that.toFooterMsg = "[WebSocket] 打开连接..." + evt;
          that.explainMsg = "WebSocket已连接";
      };

      this.WEB_SOCKET.onmessage = function (evt) {
          that.toFooterMsg = "[WebSocket] 接收到来自服务器的消息: " + evt.data;
          let receiveData = JSON.parse(evt.data);
          if(receiveData.userName == "Unity3D"){
            let methodName = receiveData.methodName;
            that.WebSocketReceiveMsg(methodName);
            let isHave = false;
            let obj = {};
            if(that.receiveMsg == null || that.receiveMsg == undefined) that.receiveMsg = [];
            for(var i = 0; i < that.receiveMsg.length; i++){
              if(that.receiveMsg[i].name == methodName) {
                isHave = true;
                obj = that.receiveMsg[i];
                break;
              }
            }
            if(isHave == true){
              obj.msgs.push(evt.data);
            }
            else{
              obj["name"] = methodName;
              obj["msgs"] = [];
              obj["msgs"].push(evt.data);
              that.receiveMsg.push(obj);
              
            }
            this.receiveFromUnityMsg = that.receiveMsg;
          }
      };

      this.WEB_SOCKET.onclose = function (evt) {
          that.toFooterMsg = "[WebSocket] 连接关闭." + evt;
          that.webSocketIsOpen = false;
          that.webSocketClose();
      };
    },
    WebSocketReceiveMsg(methodName){
      this.$notify({
          title: "Unity发来请求：",
          message: methodName,
          type: 'success'
        });
    },
    webSocketClose(){
      this.$notify({
          title: '警告',
          message: 'WebSocket已关闭！',
          type: 'warning'
        });
    },
    fromUnitRes(res){
      this.mothedsApisRes = res;
    },
   
    recordsFunc(data){
      this.recodeData = data;
      console.log(this.recodeData);
    }
  }
}
</script>